import { Component, } from '@angular/core';

@Component({
  selector: 'my-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  name = 'Teuton Calculator';

  totalResources = 0
  cropStore = 0

  totalAllocation = 0

  teutons = [
    {
      name: `Axeman`,
      wood: 130,
      clay: 120,
      iron: 170,
      crop: 70,
      upkeep: 1,
    },
    {
      name: `Teutonic Knight`,
      wood: 450,
      clay: 515,
      iron: 480,
      crop: 80,
      upkeep: 3,
    },
    {
      name: `Catapult`,
      wood: 900,
      clay: 1200,
      iron: 600,
      crop: 60,
      upkeep: 6,
    },
  ]

  recomputeAllocation() {
    this.totalAllocation = this.totalResources - this.cropStore
  }

  getUnits() {
    return this.teutons.reduce((acc, unit) => { return acc.concat(unit.name) }, [])
  }
}
